import "vant/es/checkbox-group/style/less";
import _CheckboxGroup from "vant/es/checkbox-group";
import "vant/es/checkbox/style/less";
import _Checkbox from "vant/es/checkbox";
import "vant/es/button/style/less";
import _Button from "vant/es/button";
import { computed, defineComponent, onMounted, ref } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import utils from '@/utils';
import CustimizeEmpty from '@/components/custimizeEmpty.vue';
import CustomizeSkeleton from '@/components/customizeSkeleton.vue';
export default defineComponent({
  name: 'warrent',
  components: {
    [_Button.name]: _Button,
    [_Checkbox.name]: _Checkbox,
    [_CheckboxGroup.name]: _CheckboxGroup,
    CustimizeEmpty,
    CustomizeSkeleton
  },

  setup() {
    const router = useRouter();
    const store = useStore();
    const checked = ref(false);
    onMounted(() => {
      const tokenParmas = utils.urlParser();
      store.dispatch('warrentModule/fetchAuthorization', {
        data: { ...tokenParmas
        }
      }).then(res => {
        if (res.authorization === '1') {
          router.replace({
            name: 'inter',
            query: { ...tokenParmas
            }
          });
        }
      });
    });

    const miniBack = () => {
      console.log('小程序回退');
      wx.miniProgram.navigateBack({});
    };

    const toDo = () => {
      console.log('宿主环境不是小程序');
    };

    const cancel = () => {
      typeof wx === 'object' ? miniBack() : toDo();
    };

    const confirm = () => {
      const tokenParams = utils.urlParser();
      store.dispatch('warrentModule/saveAuthorization', {
        data: { ...tokenParams
        }
      }).then(res => {
        router.replace({
          path: '/inter',
          query: { ...tokenParams
          }
        });
      }, err => {
        console.log(err);
      });
    };

    return {
      cancel,
      confirm,
      checked,
      errState: computed(() => store.state.errorState),
      placeHolder: computed(() => store.state.errPlaceHolder)
    };
  }

});